.sidebarOptions{
    display: flex;
    align-items: center;
    height: 35px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 0px 25px;
    margin-right: 20px;
}
.sidebarOptions svg{
    color: rgb(94, 94, 94);
    font-size: 20px;
    width: 20px;
}
.sidebarOptions h4{
    font-size: 14px;
    flex: 1;
    margin-left: 10px;
    font-weight: normal;
}
.sidebarOptions p{

    opacity: 1;
    color: rgb(94, 94, 94);
    font-size: 12px;
    opacity: 0;
}
.sidebarOptions:hover{
    cursor: pointer;
    background-color: whitesmoke;
}
.sidebaroptions--active {
    background-color: #fce8e6;
}
.sidebaroptions--active h4,
.sidebaroptions--active p,
.sidebaroptions--active svg 
{
    color: #d93025;
    font-weight: bold;
    opacity: 1;
}